@import url('https://fonts.googleapis.com/css?family=Raleway');
@import url('https://fonts.googleapis.com/css?family=Roboto:300');

body{
  margin: 0px;
}
/*div{
	border:1px solid black;
}*/
.mainPage{
	background: -webkit-gradient(linear,left top, left bottom,from(rgb(25,29,34)),color-stop(rgb(30,34,39)),to(rgb(36,41,47)));
	background: -webkit-linear-gradient(rgb(25,29,34),rgb(30,34,39),rgb(36,41,47));
	background: -o-linear-gradient(rgb(25,29,34),rgb(30,34,39),rgb(36,41,47));
	background: linear-gradient(rgb(25,29,34),rgb(30,34,39),rgb(36,41,47));
	width:100%;
	height: 100%;
	padding-top: 9%;
}
.heading{
	text-align: center;
	font-size: 50px;
	font-family: Yataghan-Regular;
	color: #b1ff64;
}
.club{
	width:100%;
}
.clubName{
	color: white;
	font-family: 'Lato', sans-serif;
}
.memberPhoto{
	background: lightgrey;
	width: 200px;
	height: 200px;
	
}
.content{
	margin-top: 30px;
	text-align: center;
	font-size: 50px;
}
.member{
	margin-left: 150px;
}
.memberInfo{
	color: white;
	font-size: 30px;
	font-family: 'Lato', sans-serif;
	font-weight: 300;
}
.photo{
	border-radius: 30px;
}
.members{
	margin-top: 20px;
	margin-right: 130px;
	display: -ms-inline-flexbox;
	display: inline-flex;
}

.borderBottom{
	margin: 30px auto;
	width: 80%;
	border-bottom: 1px solid grey;
}


